export default {
	data() {
		return {
			
		}
	},
	methods: {
		getUserProfile(){

			uni.getUserProfile({
				desc:"用于登录帐号",
				success:(res)=>{
					// console.log(res)
					uni.setStorageSync("userInfo",res.rawData);
					// switchTab->关闭所有非tabBar页面
					//reLaunch->关闭所有页面
					uni.reLaunch({
						url:"/pages/personal/personal"
					})
				}
			})

		}
	}
}
